import noThing from "@/components/common/no-thing.vue"
import coupon from "@/components/order-coupon/coupon.vue"
export default {
	components: {
		noThing,
		coupon
	},
	data() {
		return {
			tabIndex:0,
			price:0,
			tabBars:[
				{ 
					name:"可用",
					no_thing:"no_pay",
					msg:"你还没有可用的优惠券",
					page:1,
					key:"valid",
					firstLoad:false,
					list:[],
				},
				{
					name:"已失效",
					no_thing:"no_pay",
					msg:"你还没有优惠券",
					page:1,
					key:"invalid",
					firstLoad:false,
					list:[]
				},
			],
		}
	},
	onLoad(e) {
		if(e.detail){
			this.price = (JSON.parse(e.detail)).price
		}
		this.getData()
	},
	computed: {
		// 是否失效
		isvalid(){
			return this.tabBars[this.tabIndex].key 
		},
		// 当前页码
		page() {
			return this.tabBars[this.tabIndex].page 
		},
		validText(){
			return this.tabIndex === 0 ? '不可用' : '已失效'
		}
	},
	methods: {
		getData(){
			let index = this.tabIndex
			this.$H.get('/usercoupon/'+this.page+'/'+this.isvalid,{},{
				token:true
			}).then(res=>{
				this.tabBars[index].list = res.map(item=>{
					let status = (index === 0) && (this.price >= parseFloat(item.coupon.min_price))
					return {
						id:item.id,
						title:item.coupon.name,
						start_time:item.coupon.start_time,
						end_time:item.coupon.end_time,
						price:item.coupon.value,
						desc:item.coupon.desc,
						status:status,
						disabled:item.used,
						type:item.coupon.type
					}
				})
				this.tabBars[index].firstLoad = true
			})
		},
		// 切换选项卡
		changeTab(index){
			this.tabIndex = index
			if(!this.tabBars[index].firstLoad){
				this.getData()
			}
		},
		// 选择优惠券
		chooseCoupon(item){
			// 已使用
			if(item.disabled){
				return uni.showToast({
					title: '该优惠券已经使用过了',
					icon: 'none'
				});
			}
			// 已失效/不可用
			if(!item.status){
				return uni.showToast({
					title: '该优惠券'+this.validText,
					icon: 'none'
				});
			}
			uni.$emit('couponUser',{
				id:item.id,
				type:item.type,
				value:item.price
			})
			uni.navigateBack({
				delta: 1
			});
		}
	}
}
